.nav {
  grid-area: nav;
  background-color: burlywood;
  padding: 20px;
}

.item  {
  color: #fff;
  margin-bottom: 10px;
}

.item a.active {
  color: #0d9aef;
}

.avatarFriends {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.friendWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.avatar {
  padding: 10px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.friendName {
  margin: 0;
  padding: 0 5px 0;
}
