* {
  box-sizing: border-box;
  color: inherit;
}

a {
  text-decoration: none;
}

.app-wrapper {
  height: 100vh;
  width: 1480px;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
    "header header"
    "nav content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  grid-area: content;
  background-color: cornflowerblue;
}